import React, { Component } from 'react';
import '../../App.css';
import './style.css';

export default class SnakeGame extends Component {
  constructor(props){
    super(props)
    const grid = [];
    for(let row = 0; row < 19; row++){
      let cols = [];
      for(let col = 0; col < 19; col++){
         cols.push({ row, col })
      }
      grid.push(cols)
    }
    this.state = {

      grid,
      playing: false,
      apple: {
        row: Math.floor(Math.random() * 19),
        col: Math.floor(Math.random() * 19),
      },
      countToReload: 3,
      isPressed: false,
      snake: {
        head: {
          row: 9,
          col: 9
        },
        direction: {
          x: 1,
          y: 0,
        },
        body: [],
      },
      score: 0,

    }
    this._isMounted = false;
  }

  boostSpeed = (event) => {
    const {boost, updateBoost} = this.props;
    if(event.code === 'Space' && /[1]/gi.test(boost)) {
      this.setState({isPressed: true})
      updateBoost('0');
      const speed = this.props.speed;
      this.props.changeSpeed(this.props.speed / 2);
      setTimeout(()=> {


        this.setState({isPressed: false})
        this.props.changeSpeed(speed)
      }, 5000)

    }

    return
  }

  componentDidMount = () => {
    this._isMounted = true;
    document.addEventListener('keydown', (e)=> {
      this.setDirection(e);
      this.boostSpeed(e);

    });

  }
  setSpeed = () => {
    if(this.props.speed < 30) {
      this.props.changeSpeed(30);
    }
    if(this.appleEat()) {
      this.props.changeSpeed(this.props.speed - 20)
      if(this.appleEat() && this.state.isPressed) {
        this.props.changeSpeed(this.props.speed)
      }
    }

      return this.props.speed
  }
  componentDidUpdate = (prevProps, prevState, snapshot) => {
    if(prevProps.start === !this.props.start){
       this.gameStart();
    }
  }
  gameStart = () => {

    if(this.state.gameOver) return;
    this.props.playing(true)

    this.setState(({snake, apple, playing})=> {
      const eat = this.appleEat();
      const nextState = {
        playing:true,
        snake: {
          ...snake,
          head: {
            row: snake.head.row + snake.direction.y,
            col: snake.head.col + snake.direction.x,
          },
          body: [snake.head, ...snake.body]
        },
        apple: eat ? {
          row: Math.floor(Math.random() * 19),
          col: Math.floor(Math.random() * 19),
        } : apple
      }
      if(!eat) nextState.snake.body.pop();
      return nextState
    }, () => {
      const {snake, playing} = this.state;

      if(this.gameOver() || this.isTail(snake.head)){
        this.props.playing(false);
        this.props.gameOver(true);
        this.props.maxScore(snake.body.length);
        clearInterval(this.gameStart)
        this.setState({
          gameOver: true,
          playing:false,

        })

        return;
      }
      setTimeout(()=>{
        this.updBoost()
        this.gameStart();
      }, this.setSpeed());
    });
  }



  getAppleOnField = () => {
    const {snake} = this.state;
    const newApple = {
      row: Math.floor(Math.random() * 19),
      col: Math.floor(Math.random() * 19),
    };
    if(this.isTail(this.state.apple) || (snake.head.row === this.state.apple.row && snake.head.col === this.state.apple.col)) {
      return this.getAppleOnField();
    }
      return this.setState({
        apple: newApple
      })


  }
  gameOver  = () => {
    const {snake} = this.state;
    if (snake.head.col < 0
      ||snake.head.row < 0
      ||snake.head.col > 18
      ||snake.head.row > 18)
      {
      this.props.gameOver(true)
      this.props.maxScore(snake.body.length)
      return true
      }
      return false

  }

  appleEat = () => {
    const {apple, snake} = this.state;
    this.props.score(snake.body.length);

    return apple.row === snake.head.row && apple.col === snake.head.col;
  }
  setDirection  = (e) => {
    const {snake} = this.state;
    if(e.keyCode === 40){
      if(snake.direction.y === -1) return;
      this.setState( ({snake}) => ({
        snake: {
          ...snake,
          direction: {
            x: 0,
            y: 1,
          }
        }
      }))
    }
    else if(e.keyCode === 39){
      if(snake.direction.x === -1) return;
      this.setState( ({snake}) => ({
        snake: {
          ...snake,
          direction: {
            x: 1,
            y: 0,
          }

        }
      }))
    }
    else if(e.keyCode === 38){
      if(snake.direction.y === 1) return;
      this.setState( ({snake}) => ({
        snake: {
          ...snake,
          direction: {
            x: 0,
            y: -1,
          }
        }
      }))
    }
    else if(e.keyCode === 37){
      if(snake.direction.x === 1) return;
      this.setState( ({snake}) => ({
        snake: {
          ...snake,
          direction: {
            x: -1,
            y: 0,
          }
        }
      }))
    }
  }
  isSnake = (cell)=> {
    const {snake} = this.state;
    return cell.row === snake.head.row && cell.col === snake.head.col ? 'snake head' : ''
  }
  isApple = (cell) => {
        const {apple} = this.state;
        return cell.row === apple.row && cell.col === apple.col ? 'apple' : '';
      }
  isTail = (cell)=> {
    const {snake} = this.state;

    return snake.body.find(inBody => inBody.row === cell.row && inBody.col === cell.col)
  }

  startAgain = (e) => {
    this.props.changeSpeed(this.props.baseSpeed)
    this.props.updateCount(3)
    this.props.updateBoost('1')
    this.setState( ({snake, gameOver}) => {
            return ({
              playing: false,
              gameOver: false,
              snake:{
                head: {
                  col: 9,
                  row: 9
                },
                body: [],
                direction: {
                  x: 1,
                  y: 0,
                }
              }
            }

          )
        })
        this.props.gameOver(false)
  }
  updBoost = () => {

    if(this.appleEat()) {
      this.setState({ countToReload: this.state.countToReload - 1})
      this.props.updateCount(this.props.boostCount - 1)
      
      return
    }
    if( this.state.countToReload === 0) {
        this.setState(this.setState({ countToReload: 3}))
        this.props.updateCount(3)
       return this.props.updateBoost('1');
    }
    return false

  }
 componentWillUnmount = () => {
   this._isMounted = false;
    clearInterval(this.gameStart)
    clearInterval(this.boostSpeed)


    document.removeEventListener('keydown', (e)=> {
      this.setDirection(e);
      this.boostSpeed(e);

    });
 }
  render(){
    const {grid, snake, gameOver, playing} = this.state;
    return (
      <div id="snake" tabIndex = "0" onKeyPress={this.setDirection}>
        {
           <div id='gridWrap'>

              <section id='grid'>
                    {
                      this.state.grid.map( row => (
                        row.map( cell => (
                          <div
                            key={`${cell.row} ${cell.col}`}
                            className={`cell ${
                              this.isSnake(cell)
                              ?'head':this.isApple(cell)
                              ? 'apple' : this.isTail(cell)
                              ? 'tail' : ''
                            }`}>
                          </div>
                        ))
                      ))
                    }

                </section>
                <div >
                  {
                    !playing
                    ? (
                      <div className='modal'>
                        <button onClick={this.startAgain} id='start' >{gameOver ?"Start Again" :  'Start game'}</button>
                      </div>
                    )
                    : ''
                  }
                </div>

            </div>

        }

      </div>
    )

  }


}

